<template>
    <div>
        <div class="search-result" > 956</div>
        <el-table :data="waitAuditList" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" ></el-table-column>
            <el-table-column prop="typeName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditTime" label="" align="center"></el-table-column>
            <el-table-column prop="auditRemark" label="" align="center"></el-table-column>
            <el-table-column prop="endReason" label="" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="audit(scope.$index, scope.row)" type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 2, 3, 400]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
export default {
    name:'waitAudit',
    data(){
        return {
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:400//
            },
            waitAuditList:[],
            tableData:[
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                }
            ]
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
            this.requestList();
        },
        audit(index,row){
            this.$router.push({name:"ProfessionEndVerify",query:{id:row.id}})
        },
        // ;
        requestList(){
            let self=this;
            let requestInfo={
                auditType:"2",//
                currentPage:this.pagination.currentPage,
                pageSize:this.pagination.pageSize,
                targetState:"1"//1,2.3
            }
            this.$http.post(this.$apiUrl.professionAudit.auditList,requestInfo)
                .then(function(data){
                    self.pagination.total=data.data.data.total;
                    self.waitAuditList=data.data.data.data;
                    console.log('');
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.pagination.currentPage-1)*this.pagination.pageSize,this.pagination.currentPage*this.pagination.pageSize); 
        }
    },
}
</script>

<style>

</style>





