<template lang="html">
    <el-table
        :data="listInfo"
        border
        style="width: 100%">
        <el-table-column type="expand">
            <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item :label="item.title" v-for="item in props.row.detail" :key="item.title">
                        <span>{{ item.pre}}-->{{item.now}}</span>
                    </el-form-item>
                </el-form>
            </template>
        </el-table-column>
        <el-table-column align="center" label="" prop="operatortype"></el-table-column>
        <el-table-column align="center" label="" prop="operator"></el-table-column>
        <el-table-column align="center" label="" prop="result"></el-table-column>
        <el-table-column align="center" label="" prop="operatetime"></el-table-column>
        <el-table-column align="center" label="" prop="remark"></el-table-column>
    </el-table>
</template>

<script>
export default {
    data() {
        return {
            listInfo:[],
        }
    },
    created(){
        let self=this;
        this.$http.get(this.$apiUrl.agent.record+"/1")
        .then(function(data){
            self.listInfo=data.data.data;
            self.listInfo=[
                {
                    "id": 1,
                    "agencyid": 1,
                    "optype": "",
                    "operator": "",
                    "operatortype": "",
                    "result": "",
                    "operatetime": "2018-07-03 13:54:06",
                    "remark": ",",
                    "detail": [
                        {
                            "title": "",
                            "pre": "",
                            "now": ""
                        },
                        {
                            "title":'',
                            "pre":'xxx.jpg',
                            "now":"yyy.png"
                        },
                        {
                            "title":'',
                            "pre":'',
                            "now":""
                        },
                        {
                            "title":'',
                            "pre":'100',
                            "now":"10000"
                        }
                    ]
                },
                {
                    "id": 1,
                    "agencyid": 1,
                    "optype": "",
                    "operator": "",
                    "operatortype": "",
                    "result": "",
                    "operatetime": "2018-07-03 13:54:06",
                    "remark": ",",
                    // "detail": [
                    //     {
                    //         "title": "",
                    //         "pre": "",
                    //         "now": ""
                    //     },
                    //     {
                    //         "title":'',
                    //         "pre":'xxx.jpg',
                    //         "now":"yyy.png"
                    //     },
                    //     {
                    //         "title":'',
                    //         "pre":'',
                    //         "now":""
                    //     },
                    //     {
                    //         "title":'',
                    //         "pre":'100',
                    //         "now":"10000"
                    //     }
                    // ]
                }
            ];
            console.log(self.listInfo,'');
        })
        .catch(function(err){
            console.log(err,'');
        });
    },
    methods:{
        
    },
    mounted() {
        
    }
}
</script>

<style lang="css">
.demo-table-expand {
   font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
</style>


