<template>
    <div>
        <el-form ref="form" :model="form" label-width="180px" class="gap-2">
            <el-row >
                <el-col :span="12">
                    <el-form-item label="" prop="submitName">
                        <el-input v-model="form.city"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="auditTime">
                        <el-date-picker
                            v-model="form.auditTime"
                            type="daterange"
                            align="right"
                            unlink-panels
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            value-format="yyyy-MM-dd"
                            format="yyyy-MM-dd">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="auditName">
                        <el-input v-model="form.auditName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="agencyName">
                        <el-input v-model="form.agencyName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-row :gutter="20">
            <el-col :span="2" :offset="11">
                <el-button class="reset" @click="resetForm"></el-button>
            </el-col>
            <el-col :span="2">
                <el-button type="primary" @click="search"></el-button>
            </el-col>
        </el-row>
        <div class="search-result" > {{pagination.total}}</div>
        <el-table :data="passAuditList" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" ></el-table-column>
            <el-table-column prop="typeName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditorName" label="" align="center"></el-table-column>
            <el-table-column prop="auditTime" label="" align="center"></el-table-column>
            <el-table-column prop="auditRemark" label="" align="center"></el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
export default {
    name:'auditPass',
    data(){
        return {
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:400//
            },
            passAuditList:[],
            // 
            form: {
                auditType:"2",//
                auditName:'',//
                auditTime:[],//
                agencyName:'',//
                currentPage:1,//1
                pageSize:10,//10
                submitName:'',//
                targetState:'2',//
            },
            tableData:[
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                }
            ]
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
            this.requestList();
        },
        resetForm() {
            this.$refs.form.resetFields();
        },
        search(){
            this.requestList();
        },
        // 
        requestList(){
            let self=this;
            this.$http.post(this.$apiUrl.professionAudit.auditList,this.form)
                .then(function(data){
                    self.pagination.total=data.data.data.total;
                    self.passAuditList=data.data.data.data;
                    console.log(data,'');
                })
                .catch(function(err){
                    console.log(err,'');
                });
        }
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.pagination.currentPage-1)*this.pagination.pageSize,this.pagination.currentPage*this.pagination.pageSize); 
        }
    },
}
</script>

<style>

</style>





